import React, { useEffect, useState } from "react";
import Newsiteams from "./Newsiteams";
import InfiniteScroll from "react-infinite-scroll-component";
import ScrollToTop from "react-scroll-up";
import { Rings } from "react-loader-spinner";

export default function News(props) {
  const [articles, setarticles] = useState([]);
  const [page, setpage] = useState(1);
  const [totalArticles, settotalArticles] = useState();

  console.log(props.cat);

  useEffect(() => {
    fetchMoreData();
  }, []);

  const fetchMoreData = async () => {
    
    let api = await fetch(
      `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.cat}&apiKey=1e93b7049b2743a598f2021346e4340c&page=${page}`
    );
    let parsedData = await api.json();
    console.log(parsedData.articles);
    setpage(page + 1);
    settotalArticles(articles.length);
    setarticles(articles.concat(parsedData.articles));
  };

  return (
    <>
      <br></br>
      <br></br>
      <h1 className="d-flex mt-5 justify-content-center">
        <strong>Latest Bulletin Here For You</strong>
      </h1>
      <div className="container me-1">
        <InfiniteScroll
          dataLength={articles.length}
          next={fetchMoreData}
          hasMore={articles.length != totalArticles}
          loader={
            <h4 className="d-flex justify-content-center">
              <Rings height="100" width="100" color="red" ariaLabel="loading" />
            </h4>
          }
        >
          <div className="container">
            <div className="row">
              {articles.map((value) => {
                return (
                  <div className="col-sm-3" key={value.url}>
                    <Newsiteams
                      tittle={value.title}
                      description={value.description}
                      imageUrl={value.urlToImage}
                      url={value.url}
                    />
                  </div>
                );
              })}
            </div>
          </div>
        </InfiniteScroll>
        {/* <div className="container d-flex justify-content-between">
          {/* {console.log(articles[1])} */}
        {/* <button
            key={articles[1].url}
            type="button"
            className="btn btn-primary"
            onClick={nextpage}
          >
            Info
          </button> */}
      </div>
      <ScrollToTop showUnder={160}>
        <span>
          <img src="https://img.icons8.com/external-flatart-icons-solid-flatarticons/50/000000/external-arrow-up-arrows-flatart-icons-solid-flatarticons.png" />
        </span>
      </ScrollToTop>
    </>
  );
}
