import React from "react";
import { Link } from "react-router-dom";

import "./Navbar.css";
// import React from "react";
// import Link from "gatsby-link";
import {
  Collapse,
  Container,
  Navbar,
  NavbarToggler,
  Nav,
  NavItem,
} from "reactstrap";

export default class extends React.Component {
  constructor(props) {
    super(props);
    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.closeNavbar = this.closeNavbar.bind(this);
    this.state = {
      collapsed: true,
    };
  }

  toggleNavbar() {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  }

  closeNavbar() {
    if (this.state.collapsed !== true) {
      this.toggleNavbar();
    }
  }


  render() {
    return (
      <div>
        <nav className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark">
          <div className="container-fluid">
            <Link
              className="navbar-brand"
              id="nav"
              onClick={this.closeNavbar}
              to="/"
            >
              <strong>
                <h3 className="text-warning bg-dark">Bulletin</h3>
              </strong>
            </Link>
            <button
              className="navbar-toggler"
              onClick={this.toggleNavbar}
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
              isOpen={!this.state.collapsed}

            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div
              className="collapse navbar-collapse "
              // onClick={this.closeNavbar}
              isopen={this.state.collapsed && !this.state.collapsed }
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav mx-auto mb-2 mb-lg-0 ">
                <li className="nav-item naviteams">
                  <Link
                    className="nav-link active"
                    onClick={this.closeNavbar}
                    aria-current="page"
                    to="/sports"
                  >
                    <h5>Sports</h5>
                  </Link>
                </li>
                <li className="nav-item naviteams">
                  <Link
                    className="nav-link active"
                    aria-current="page"
                    to="/health"
                  >
                    <h5>Health</h5>
                  </Link>
                </li>
                <li className="nav-item naviteams">
                  <Link
                    className="nav-link active"
                    to="/business"
                    onClick={this.closeNavbar}
                  >
                    {" "}
                    <h5>Business</h5>
                  </Link>
                </li>
                <li className="nav-item naviteams">
                  <Link
                    className="nav-link active"
                    to="/entertainment"
                    onClick={this.closeNavbar}
                  >
                    {" "}
                    <h5>Entertainment</h5>
                  </Link>
                </li>
                <li className="nav-item naviteams">
                  <Link
                    className="nav-link active"
                    to="/technology"
                    onClick={this.closeNavbar}
                  >
                    {" "}
                    <h5>technology</h5>
                  </Link>
                </li>
                <li className="nav-item naviteams">
                  <Link
                    className="nav-link active"
                    to="/science"
                    onClick={this.closeNavbar}
                  >
                    {" "}
                    <h5>science</h5>
                  </Link>
                </li>
              </ul>
              <div className="dropdown">
                <button
                  className="btn btn-secondary dropdown-toggle rounded-pill"
                  type="button"
                  id="dropdownMenuButton1"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Country
                </button>
                <ul
                  className="dropdown-menu"
                  aria-labelledby="dropdownMenuButton1"
                >
                  <li>
                    <Link className="dropdown-item" to="/in" country="in">
                      India
                    </Link>
                  </li>
                  <li>
                    <Link className="dropdown-item" to="/us" country="us">
                      US
                    </Link>
                  </li>
                  <li>
                    <Link className="dropdown-item" to="/ch" country="ch">
                      China
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </nav>
      </div>
    );
  }
}
